<template>
  <div>
    <!-- Header -->
    <div
      class="header py-7 py-lg-8 pt-lg-9"
      style="background-image: url('img/theme/img-background-login.png'); background-size: auto 50vh"
    >
      <div class="separator separator-bottom separator-skew zindex-100">
        <svg
          x="0"
          y="0"
          viewBox="0 0 2560 100"
          preserveAspectRatio="none"
          version="1.1"
          xmlns="http://www.w3.org/2000/svg"
        >
          <polygon class="fill-default" points="2560 0 2560 100 0 100" />
        </svg>
      </div>
    </div>
    <!-- Page content -->
    <div class="container mt--8 pb-5">
      <div class="row justify-content-center">
        <div class="col-lg-5 col-md-7">
          <div class="card bg-white border-0 mb-0">
            <div class="card-header bg-transparent d-flex justify-content-center">
              <img src="img/brand/img-logo-color.png" alt="Login Logo" style="height: 4rem" />
            </div>
            <div class="card-body px-lg-5 py-lg-5">
              <validation-observer ref="formValidator">
                <form role="form">
                  <base-input
                    alternative
                    class="mb-3"
                    name="Email"
                    :rules="{required: true, email: true}"
                    prepend-icon="ni ni-circle-08"
                    placeholder="Email"
                    v-model="model.email"
                  ></base-input>

                  <base-input
                    alternative
                    class="mb-3"
                    name="Password"
                    :rules="{required: true, min: 6}"
                    prepend-icon="ni ni-lock-circle-open"
                    type="password"
                    placeholder="Password"
                    v-model="model.password"
                  ></base-input>
                  <div class="text-center">
                    <base-button
                      type="primary"
                      native-type="submit"
                      @click="login"
                      class="my-4"
                    >Sign in</base-button>
                  </div>
                </form>
              </validation-observer>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import axios from "axios";
export default {
  data() {
    return {
      model: {
        email: "",
        password: "",
        rememberMe: false,
      },
    };
  },
  beforeMount() {
    var token = JSON.parse(localStorage.getItem("auth-data"));
    !token
      ? () => {
          this.$store.getters.isDebugging
            ? console.log("DEBUG: Failed to get auth data.")
            : console.log();
        }
      : this.$router.push("/ispu/dashboard");
  },
  methods: {
    login() {
      axios
        .post(this.$store.getters.getAPI.url + "/user/login", this.model)
        .then((res) => {
          var auth = JSON.stringify(res.data);
          localStorage.setItem("auth-data", auth);
          this.$store.commit("setUserData", res.data);
          this.$notify({
            message: "Welcome to <b>IPC IEMS Platform</b>",
            timeout: 5000,
            icon: "ni ni-bell-55",
            type: "success",
          });
          this.$router.push("/ispu/dashboard").catch(() => {});
        })
        .catch((err) => {
          this.$notify({
            message: "Invalid Email or Password",
            timeout: 5000,
            icon: "ni ni-bell-55",
            type: "warning",
          });
        });

      // this will be called only after form is valid. You can do api call here to login
    },
  },
};
</script>

<style lang="scss" scoped>
.separator-bottom {
  bottom: -2px;
}
</style>
